// Programming10.tsx

import React, { useEffect} from 'react';
import { useLocation } from 'react-router-dom';
import { LinkP, JumpToDefaultScroll, SketchCode, CodeBlock, Img } from '../ContentCommon';
import { MathJaxProvider, MathJaxFormula } from 'mathjax3-react';
import '../ContentCommon.css';

const imageFolder: string = "/musabi-react/images/programming/";

interface ContentProps {
  practiceId: string;
}

const Programming10: React.FC<ContentProps> = ({ practiceId }) => {

  /* URLから初期スクロール位置 */
  const location = useLocation();
  useEffect(() => {
    JumpToDefaultScroll(practiceId, location as any );    
  }, [practiceId,location]);
  return (
    <div className="common-style">
      <h1 id='h0'>第１０回：プログラミング環境</h1>
        <h2 id='静止画の出力と再描画'>静止画の出力と再描画</h2>
        <p>ウィンドウ上でマウスボタンをクリックすることで静止画の再描画とpngファイルの出力を行う．</p>
        <Img src={`${imageFolder}save_redraw.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 400 );\n  noLoop();\n}\n\nvoid draw()\n{\n  background( 204 );  // スクリーンリフレッシュ用（色は任意）\n  \n  /** 以降に描画プログラムを記述する */\n  \n  background( random( 255 ) );  // テスト用なので消してよい．\n  \n\n  save("kadai.png");	// png保存\n}\n\nvoid mouseClicked()\n{\n  redraw();	// 再度draw()を実行する．\n}'}</CodeBlock>
        <h3 id='使い方-1'>使い方</h3>
        <p>ウィンドウ上でマウスボタンをクリックする．</p>
        <h3 id='説明-51'>説明</h3>
        <ul>
        <li><p>クリックするだけでランダム値によるバリエーションを出力できる．</p>
        </li>
        <li><p>再描画の際は<code>draw()</code>が再度１度だけ実行され，<code>randam()</code>におけるランダム値も新しい値が取得される．</p>
        </li>
        <li><p>出力ファイル名を変える場合は<code>save("kadai.png")</code>を書き換える．</p>
        <ul>
        <li><p><code>jpg</code>（JPEGファイル）も可能．</p></li>

        </ul>
        </li>

        </ul>
        <h3 id='留意点'>留意点</h3>
        <ul>
        <li><p>事前にスケッチ（プログラム）を適当な場所に保存し，スケッチブック（フォルダ）を生成しておく必要がある．</p></li>
        <li><p>アニメーションと同様にスクリーンのリフレッシュ処理が記述されている．</p></li>
        <li><p>自作の関数も使える．</p></li>

        </ul>
        <h3 id='再帰関数用テンプレート'>再帰関数用テンプレート</h3>
        <p>再帰関数プログラム（静止画出力と再描画機能付き）のテンプレート</p>
        <CodeBlock>{'void setup()\n{\n  size( 400, 400 );\n  noLoop();\n}\n\nvoid draw()\n{\n  background( 204 );  // スクリーンリフレッシュ用\n  \n  // 最初の再帰呼び出し\n  drawRecursion( 5 );\n  \n  save("kadai.png");  // png保存\n}\n\nvoid drawRecursion( int iLevel )\n{\n  /** 主にここで描画を行う */\n  \n  if( iLevel > 1 )\n  {\n    drawRecursion( iLevel - 1 );\n  }\n}\n\nvoid mouseClicked()\n{\n  redraw();\n}'}</CodeBlock>
        <h3 id='例-33'>例</h3>
        <p>ランダム値を用いた再帰木の静止画出力と再描画</p>
        <Img src={`${imageFolder}save_redraw_tree.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 400 );\n  noLoop();\n}\n\nvoid draw()\n{\n  background( 204 );  // スクリーンリフレッシュ用\n  \n  // 最初の再帰呼び出し\n  drawTree( width/2, height, 80, -90, 12 );\n  \n  save("kadai.png");	// png保存\n}\n\nvoid drawTree( float fBeginX, float fBeginY, float fLength, float fDegree, int iLevel )\n{\n  // 終端のXY座標値\n  float fEndX = fBeginX + fLength * cos( radians( fDegree ) );\n  float fEndY = fBeginY + fLength * sin( radians( fDegree ) );\n  \n  // 線の描画\n  line( fBeginX, fBeginY, fEndX, fEndY );\n  \n  if( iLevel > 1 )\n  {\n    drawTree( fEndX, fEndY, fLength * random( 0.60, 0.8 ), fDegree + random( 16, 24 ), iLevel - 1 );\n    drawTree( fEndX, fEndY, fLength * random( 0.60, 0.8 ), fDegree - random( 16, 24 ), iLevel - 1 );\n  }\n}\n\nvoid mouseClicked()\n{\n  redraw();\n}'}</CodeBlock>
        <h2 id='h1'>関数を別ファイルに分ける．</h2>
        <p>関数を別ファイルに分けるとプログラム行数が減って見やすくなる．</p>
        <p>前述の例「ランダム値を用いた再帰木の静止画出力と再描画」を別ファイルに分けた例</p>
        <Img src={`${imageFolder}divide_sourcefiles.gif`} />
        <h3 id='1-新規タブを作成する'>1. 新規タブを作成する</h3>
        <ul>
        <li><p>エディターのタブ右端の▼アイコンをクリック &gt; 新規タブ</p></li>
        <li><p>切り分ける関数が分かりやすい名前を付ける．</p></li>
        <li><p>新たにpdeファイルがフォルダ内に生成される．</p></li>

        </ul>
        <Img src={`${imageFolder}create_new_tab_recursion.gif`} />
        <h3 id='2-関数定義部分を新規タブにカットペーストする'>2. 関数定義部分を新規タブにカット＆ペーストする．</h3>
        <p>メインのプログラムのタブから，新規タブへ関数定義部分を移植する．</p>
        <Img src={`${imageFolder}cut_and_paste_recursion.gif`} />
        <h3 id='新規タブの削除のやり方'>新規タブの削除のやり方</h3>
        <p>削除したいプログラムのタブを選択し，エディターのタブ右端の▼アイコンをクリック &gt; 削除</p>
        <Img src={`${imageFolder}delete_tab.gif`} />
    </div>
  );
};

export default Programming10;
